.switch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 26px;
  border: 2px solid #bbbbbb;
  border-radius: 20px;
}

.switch::after {
  content: '';
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #bbbbbb;
  top: 2px;
  left: 2px;
  transition: all 0.3s;
}

.checkbox:checked + .switch:after {
  left: 20px;
  background-color: #fb3f5c;
  background-color: var(--fml-secondary);
}

.checkbox:checked + .switch {
  border-color: #fb3f5c;
  border-color: var(--fml-secondary);
}

