

  .top-image {
    background-image: linear-gradient(rgba(4,23,42,0.5), rgba(4,23,42,0.5)),
    url(../contact/woman.png);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   
  }
  .top-image__title {
    margin-top: 72px;
    margin-bottom: 17px;
  }
  .top-image__text {
    font-weight: 500;
    font-size: 20px;
    color: #fff;
    line-height: 28px;
    text-align: center;
    margin: 17px 400px 72px 400px;
  }
  .back-wrap {
    background-color: #e5e5e5;
  }
  .back-wrap2 {
  opacity: 0;
  }
  .back-wrap3 {
    opacity: 0;
    }
  #snackbar {
    background-color: rgba(251, 63, 91, 0.932);
    min-width: 250px; 
    color: #fff; 
    font-size: 1rem;
    text-align: center;
    border-radius: 2px;
    padding: 5px;
    position: fixed;
    transition: all 1.3s ease-in-out!important;
    z-index: 1; 
    left: 50%; 
    top: 20%;
  }
  #snackbar2 {
    background-color: rgba(251, 63, 91, 0.932);
    min-width: 250px; 
    color: #fff; 
    font-size: 1rem;
    text-align: center;
    border-radius: 2px;
    padding: 5px;
    position: fixed;
    transition: all 1.3s ease-in-out!important;
    z-index: 1; 
    left: 50%; 
    top: 20%;
  }
  .main-wrap {
    display: grid;
    grid-template-columns: 30vw 1fr;
    padding-top: 65px;
    padding-bottom: 65px;
    padding: 65px 100px !important;
  }
  .contact-item {
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0px 0px 3.20094px rgba(0, 0, 0, 0.25);
    width: 300px;
  }
  .contact--wrap {
    justify-self: end !important;
  }
  .contact-item__title {
    font-size: 19px;
    font-weight: 500;
  }
  .contact-item__num {
    color: #999898;
    font-size: 13px;
  }
  .form--wrap {
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0px 0px 3.20094px rgba(0, 0, 0, 0.25);
  }
  .form--case {
    padding: 30px 30px 100px 30px
  }
 
   
  #snackbar.shows {
     opacity: 1!important; 
   }
   #snackbar2.shows2 {
    opacity: 1!important; 
  }
   
 
  .form-item--input {
    width: 20rem;
    border: 0.800236px solid rgba(4, 23, 42, 0.7);
    border: 4px;
  }
  .form-item--text-area {
    height: 165px !important;
    border: 0.800236px solid rgba(4, 23, 42, 0.2);
    border-radius: 4px;
    width: 95%!important;
  }
  .form-item--input:active {
    outline: none !important;
  }
  .form-item--text-area:focus {
    outline: none !important;
  }
  .form-submit {
    background-color: #FB3F5C;
    color: #fff;
    padding: 10px 20px;
    box-shadow: 0px 3.20094px 12.0035px rgba(251, 63, 92, 0.2);
    border-radius: 4.00118px;
    border: none;
    cursor: pointer;
    float: right;
  }
  @media (max-width: 1285px) {
    .top-image__text {
      margin: 17px 100px 72px;
    }
    .form-item--input {
      width: 40rem;
    }
  }
  @media (max-width: 1100px) {
    .main-wrap {
      grid-template-columns: 40vw 1fr;
      padding: 65px 60px !important;
    }
  }
  @media (max-width: 820px) {
    .main-wrap {
      grid-template-columns: 1fr;
      padding: 65px 30px !important;
      
    }
    .form-item--text-area {
      
      width: 100%!important;
    }
    .contact--wrap {
      margin-bottom: 65px;
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
    .contact--wrap-2 {
      justify-self: end;
    }
  }
  @media (max-width: 700px) {
    .contact--wrap {
      display: flex !important;
      flex-direction: column;
    }
    .contact-item {
      width: 85vw;
    }
    .form-item--text-area {
      width: 87%!important;
    }
  }
  @media (max-width: 420px) {
    .top-image__text {
      margin: 17px 50px 72px;
    }
    .main-wrap {
      padding: 65px 10px !important;
    }
    .contact--wrap {
      padding-left: 5px;
    }
    .contact-item {
      width: 90vw;
    }
    .contact-item__group {
      margin-right: 20px;
    }
  }